const BASE_URL = 'http://127.0.0.1:5000/';

const SYMBOLS = '/symbols/search/';


export function getQuote(ticker) {

}

export function getSymbols(ticker) {
	const Http = new XMLHttpRequest();
	const url = BASE_URL + SYMBOLS + ticker;
	Http.open("GET", url);
	return Http.send();
}


